import { startCase } from 'lodash';

type Reducer = (s: string) => string;

const removeExtension = (s: string): string => s.replace(/\.\S*$/, '');

const trimConsequentSpaces = (s: string): string => s.replace(/\s+/g, ' ');

const removeSpecialChars = (s: string): string =>
  s.replace(/[^a-zA-Z0-9]/g, ' ');

const lowercaseAllButFirst = (s: string): string => {
  const [first, ...rest] = s.split(' ');

  return [first, ...rest.map((w) => w.toLowerCase())].join(' ');
};

const toAltText = (s: string): string => {
  const reducers: Reducer[] = [
    decodeURIComponent,
    removeExtension,
    removeSpecialChars,
    trimConsequentSpaces,
    startCase,
    lowercaseAllButFirst,
  ];

  return reducers.reduce<string>((acc, fn) => fn(acc), s);
};

export { toAltText };
